@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  width: 100%;
}

.app {
  font-family: 'Lato', sans-serif;
  width: 100%;
}

#education, #contact {
  margin-top: 50px;
  margin-bottom: 50px;
}

#projects {
  margin-top: 20px;
}

#contact p {
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
}

.center-content {
  margin-left: auto;
  margin-right: auto;
  width: 80vw;
}
#navbar {
    width: 100%;
}

#navbrand {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: white;
    font-size: calc(1em + 0.5vw);
}

.navbar-toggler {
    background-color: white !important;
}

.color-nav {
    background-color: #2D5273;
}

.navLink {
    color: white !important;
    font-size: calc(1em + 0.5vw);
    margin-right: 10px;
}

.navLink:hover {
    color: black !important;
    border-radius: 10px;
    background-color: white;
}
footer {
    text-align: center;
    font-size: calc(1em + 0.3vw);
    background-color: #2D5273;
    color: white;
    margin-top: 20px;
}
#about {
    margin-top: 50px;
}

#intro-icons {
    margin-top: 10px;
}

.tech-logo {
    height: 30px;
    margin: 5px;
}

#techs {
    background-color: #efefef;
    padding: 20px;
}

p {
    font-size: calc(1em + 0.5vw);
}
.about-ico {
    height: 30px;
    margin-left: 10px;
    margin-right: 10px;
}
.viewProjectBtn {
    background-color: #2D5273;
    border-radius: 5px;
    padding: 5px;
    font-size: calc(1em + 0.4vw);
    color: white;
    width: 100px;
}

.viewProjectBtn:hover {
    color: white;
    font-size: 30px;
}
#experience {
    margin-top:100px;
}
