#navbar {
    width: 100%;
}

#navbrand {
    display: flex;
    align-items: center;
    color: white;
    font-size: calc(1em + 0.5vw);
}

.navbar-toggler {
    background-color: white !important;
}

.color-nav {
    background-color: #2D5273;
}

.navLink {
    color: white !important;
    font-size: calc(1em + 0.5vw);
    margin-right: 10px;
}

.navLink:hover {
    color: black !important;
    border-radius: 10px;
    background-color: white;
}